import React from 'react';
import me from '../img/me.png'

export default () => {
  return (
    <div className="home">
      <div className="home-content">
        <p>Hello, 我是肖俊，欢迎来到我的主页。</p>

        <p>我毕业于中山大学，并完成本科和博士的学习，之后去到美国进行博士后深造。</p>
      
        <p>从美国回来后，我决定转行互联网，开始自学互联网技术。</p>

        <p>经过学习，我完成了三个个人项目, 分别是EDSolver, LALSCI和Ni's Website。EDSolver是一款在线晶体衍射标定工具，LALSCI是一个关于激光液相烧蚀领域的文献检索和分类平台，Ni's Website是为香港中文大学Ni Zhao教授制作的个人主页。</p>

        <p>自学期间，我接触到很多知名在线教育平台的产品，如Udemy，Coursera，慕课，网易云课堂等，是这些平台的重度用户。同时我对在线教育领域有了一定的了解，也产生了很大的兴趣，乐于从事在线教育的相关工作。</p>
        
        <p>热爱教育，热爱互联网。</p>
      </div>
      
      <div className="div-App-me">
        <img className="App-me" src={me} alt="me"/>
      </div>
          
    </div>
  )
}


